<template>
  <div class="bookrack">
    <loading v-show="isLoading" />
    <div v-if="isShow" class="bookrack-content content-1">
      <div class="imgBox">
        <img class="auto-img" src="../../assets/images/books.png" alt="图片未加载..." />
      </div>
      <p class="text">把时间花在美好的事物上</p>
      <p class="text">让阅读变成一种习惯</p>
      <p class="text">去书城逛逛吧</p>
    </div>
    <div v-else class="bookrack-content clearfix">
      <div
        class="book-box fl"
        @click="goBook(item._id)"
        v-for="(item,index) in bookList"
        :key="index"
      >
        <div class="img-box">
          <img :src="getImg(item.cover)" alt="图片加载ing..." />
        </div>
        <div class="text">{{item.title}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import loading from "../../components/loading";
import "../../assets/css/bookrack.less";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("saveDataFn");
export default {
  components: { loading },
  data() {
    return {
      isShow: true,
      idList: [],
      bookList: [],
      isLoading: false,
      bookRackData: []
    };
  },
  created() {
    if (localStorage.getItem("bookRackData")) {
      let bookRackData = localStorage.getItem("bookRackData").split(",");
      if (bookRackData.length > 1) {
        bookRackData.forEach(v => {
          this.bookRackData.push(v);
        });
      } else {
        this.bookRackData = [bookRackData];
      }
      if (this.bookRackData.length > 0) {
        this.isShow = false;
        this.idList = this.bookRackData;
        this.isLoading = false;
        this.getBook();
      }
    }
  },
  methods: {
    // 获取加入书架的数据
    getBook() {
      this.isLoading = false;
      for (let i = 0; i < this.idList.length; i++) {
        this.axios({
          method: "get",
          url: "http://novel.kele8.cn/book-info/" + this.idList[i]
        })
          .then(res => {
            if (res.status == 200) {
              this.isLoading = false;
              this.bookList.push(res.data);
            }
          })
          .catch(err => {
            this.isLoading = false;
          });
      }
    },
    getImg(val) {
      return unescape(val).slice(7);
    },
    goBook(id) {
      this.$router.push({ name: "Book", query: { id } });
    }
  },
  computed: {
    ...mapState(["bookCache"])
  }
};
</script>
